import { createGlobalStyle } from 'styled-components';

const GlobalStyle = createGlobalStyle`
  /* Font loading handled in Dashboard.tsx via link tag */
  
  :root {
    /* Responsive font size deikenleri */
    --font-size-base: 18px; /* Arttrld */
    --font-size-large: 1.8rem;
    --font-size-xlarge: 2.2rem; /* Balk iin daha byk */
    --font-size-xxlarge: 2.8rem;
    --font-size-xxxlarge: 3.5rem;
  }

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html {
    font-size: var(--font-size-base);
  }

  body {
    font-family: 'Montserrat', 'Arial', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #0d1b2a; /* ok koyu lacivert arka plan */
    color: #ffffff; /* Beyaz yaz */
    overflow: hidden;
    font-weight: 700; /* Daha kaln varsaylan */
  }

  #root {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }

  /* Ensure no scrollbars for kiosk mode */
  ::-webkit-scrollbar {
    display: none;
  }

  /* For older browsers */
  html {
    scrollbar-width: none;
    -ms-overflow-style: none;
  }

  /* Android TV ve byk ekranlar iin responsive ayarlar */
  @media screen and (min-width: 1920px) {
    html {
      font-size: 22px;
    }
  }

  /* Orta boy ekranlar iin */
  @media screen and (max-width: 1919px) and (min-width: 1280px) {
    html {
      font-size: 18px;
    }
  }

  /* Kk ekranlar iin */
  @media screen and (max-width: 1279px) {
    html {
      font-size: 16px;
    }
  }
`;

export default GlobalStyle; 