import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import reportWebVitals from './reportWebVitals';

// Viewport leklendirme kontrol
const setViewportScale = () => {
  // Cihaz tipi ve boyut tespiti
  const isLargeScreen = window.innerWidth >= 1920;
  const isMediumScreen = window.innerWidth >= 1280 && window.innerWidth < 1920;
  
  // Viewport meta etiketini ayarla
  const viewportMeta = document.querySelector('meta[name="viewport"]');
  if (viewportMeta) {
    // Daha iyi okunabilirlik iin leklendirme devre d braklyor
    viewportMeta.setAttribute('content', 
      'width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no');
  }
  
  // Ekran boyutuna gre font boyutunu ayarla
  if (isLargeScreen) {
    document.documentElement.style.fontSize = '22px';
  } else if (isMediumScreen) {
    document.documentElement.style.fontSize = '18px';
  } else {
    document.documentElement.style.fontSize = '16px';
  }
  
  // Daha iyi okunabilirlik iin text-rendering zelliini ayarla
  document.body.style.textRendering = 'optimizeLegibility';
  // TypeScript tipini zmek iin CSS'i direkt olarak ayarlayalm
  const bodyStyle = document.body.style as any;
  bodyStyle['-webkit-font-smoothing'] = 'antialiased';
  bodyStyle['-moz-osx-font-smoothing'] = 'grayscale';
};

// Sayfa yklendiinde ve pencere boyutu deitiinde viewport leini ayarla
if (typeof window !== 'undefined') {
  window.addEventListener('load', setViewportScale);
  window.addEventListener('resize', setViewportScale);
}

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
