import axios from 'axios';
import { TransportRecord } from '../types';

import { createClient } from '@supabase/supabase-js';

const SUPABASE_URL = process.env.REACT_APP_SUPABASE_URL;
const SUPABASE_API_KEY = process.env.REACT_APP_SUPABASE_API_KEY;

if (!SUPABASE_URL || !SUPABASE_API_KEY) {
  console.error('Missing environment variables:', {
    SUPABASE_URL: SUPABASE_URL ? 'Set' : 'Not set',
    SUPABASE_API_KEY: SUPABASE_API_KEY ? 'Set' : 'Not set'
  });
  throw new Error('Missing required environment variables for Supabase API');
}

export const supabase = createClient(SUPABASE_URL, SUPABASE_API_KEY);

export const fetchTransportRecords = async (): Promise<TransportRecord[]> => {
  try {
    console.log('Fetching from Supabase via client...');
    const { data, error } = await supabase
      .from('06global')
      .select('*')
      .order('SIRA', { ascending: true, nullsFirst: false });

    if (error) {
      throw error;
    }

    console.log('Supabase records:', data);
    return data as TransportRecord[];
  } catch (error: any) {
    console.error('Error fetching transport records:', error.message);
    throw error;
  }
};

export const fetchExtraInfo = async (): Promise<any[]> => {
  try {
    console.log('Fetching extra info from Supabase via client...');
    const { data, error } = await supabase
      .from('06globalekbilgi')
      .select('*');

    if (error) {
      throw error;
    }

    console.log('Extra info data:', data);
    return data || [];
  } catch (error: any) {
    console.error('Error fetching extra info:', error.message);
    return [];
  }
};

const supabaseService = {
  fetchTransportRecords,
  fetchExtraInfo,
  supabase,
};

export default supabaseService;
