// Helper to generate a "Premium Monochromatic Dark" gradient
// The user wants a "high quality gradient in dark tones of the selected color".
// To achieve this without color mixing artifacts, we use the user's color as the base,
// and overlay a black gradient that goes from very dark (opaque) to less dark (sheer).
const generateGradient = (hex: string) => {
  if (!hex || hex === 'transparent') {
    return 'linear-gradient(135deg, #021024 0%, #052659 100%)';
  }

  // Strategy: "Shaded Depth"
  // Layer 1 (Top): A gradient of Black Opacity (85% black -> 30% black).
  // Layer 2 (Bottom): The User's Solid Color.
  // Result: A rich, professional gradient that is strictly a darker shade of the chosen color.

  return `
    linear-gradient(135deg, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0.3) 100%),
    linear-gradient(0deg, ${hex}, ${hex})
  `;
};

