const Dashboard: React.FC = () => {
  const [records, setRecords] = useState<TransportRecord[]>([]);
  const [extraInfo, setExtraInfo] = useState<any[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [currentTime, setCurrentTime] = useState<string>(new Date().toLocaleTimeString());
  const [isOnline, setIsOnline] = useState<boolean>(navigator.onLine);
  const [stableLayout, setStableLayout] = useState<boolean | null>(null);
  const layoutTimerRef = useRef<NodeJS.Timeout | null>(null);

  const phoneNumber = useMemo(() => {
    if (extraInfo.length > 0) {
      console.log('Searching for phone in extraInfo:', extraInfo);
      const row = extraInfo.find(e => e.telefon);
      if (row) return row.telefon;
    }
    return '0(532) 399 22 85';
  }, [extraInfo]);

  const pageTitle = useMemo(() => {
    if (extraInfo.length > 0) {
      console.log('Searching for title in extraInfo:', extraInfo);
      const row = extraInfo.find(e => e.baslik);
      if (row) return row.baslik;

      if (row) return row.baslik;
    }
    return '';
  }, [extraInfo]);

  const headerColor = useMemo(() => {
    if (extraInfo.length > 0) {
      const row = extraInfo.find(e => e.colour); // Priority to row with color? Or just first row generally
      if (row && row.colour) return row.colour;
    }
    return undefined;
  }, [extraInfo]);

  const displayRecords = useMemo(() => {
    return records.filter(record => record.TAHTA !== 'TELEFON');
  }, [records]);

  const hasActiveRecords = useMemo(() => {
    return displayRecords.some(record => record.DURUM === "Aktif" || record.DURUM === "Aktif-Uyar");
  }, [displayRecords]);

  useEffect(() => {
    const handleOnline = () => setIsOnline(true);
    const handleOffline = () => setIsOnline(false);
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  useEffect(() => {
    document.title = '06 Global Lojistik';
    // Add Favicon
    const favicon = document.querySelector('link[rel="icon"]') as HTMLLinkElement;
    if (favicon) {
      favicon.href = '/mainlogo.svg';
    } else {
      const newFavicon = document.createElement('link');
      newFavicon.rel = 'icon';
      newFavicon.href = '/mainlogo.svg';
      document.head.appendChild(newFavicon);
    }

    // Add Google Fonts
    const fontLink = document.createElement('link');
    fontLink.href = 'https://fonts.googleapis.com/css2?family=Montserrat:wght@600;700;800;900&family=Outfit:wght@700;900&display=swap';
    fontLink.rel = 'stylesheet';
    document.head.appendChild(fontLink);

    return () => {
      // Cleanup font link on unmount if needed, but usually fine to leave
      if (document.head.contains(fontLink)) {
        document.head.removeChild(fontLink);
      }
    };
  }, []);

  useEffect(() => {
    if (stableLayout === null && !loading) {
      setStableLayout(hasActiveRecords);
    }

    if (stableLayout !== null && stableLayout !== hasActiveRecords) {
      if (layoutTimerRef.current) clearTimeout(layoutTimerRef.current);
      layoutTimerRef.current = setTimeout(() => {
        setStableLayout(hasActiveRecords);
        layoutTimerRef.current = null;
      }, LAYOUT_TRANSITION_DELAY);
    }

    return () => {
      if (layoutTimerRef.current) clearTimeout(layoutTimerRef.current);
    };
  }, [hasActiveRecords, loading, stableLayout]);

  const loadData = useCallback(async () => {
    if (!navigator.onLine) {
      console.log("App is offline, skipping data fetch.");
      return;
    }

    try {
      const [recordsData, extraInfoData] = await Promise.all([
        supabaseService.fetchTransportRecords(),
        supabaseService.fetchExtraInfo()
      ]);

      console.log("Full Extra Info Data:", extraInfoData);
      setExtraInfo(extraInfoData || []);

      const mergedRecords = recordsData.map(record => {
        const extra = extraInfoData.find(e => e.baslik === record.TAHTA);
        return { ...record, TELEFON: extra ? extra.telefon : null };
      });

      setRecords(mergedRecords || []);
      if (error) setError(null);
    } catch (err: any) {
      console.error("Data fetch failed:", err.message);
    } finally {
      setLoading(false);
    }
  }, [error]);

  // Setup Realtime subscription and Polling fallback
  useEffect(() => {
    let mounted = true;

    // Initial load
    loadData();

    // Subscribe to changes
    const channel = supabaseService.supabase
      .channel('room-1')
      .on(
        'postgres_changes',
        { event: '*', schema: 'public' },
        (payload) => {
          console.log('REALTIME EVENT received:', payload);
          if (mounted) loadData();
        }
      )
      .subscribe((status) => {
        console.log('Supabase Realtime Status:', status);
      });

    // Fallback: Polling every 5 seconds to ensure data is fresh
    // (Required if Realtime Replication is not enabled on the database)
    const pollInterval = setInterval(() => {
      if (mounted) loadData();
    }, REFRESH_INTERVAL);

    // Clock interval
    const clockIntervalId = setInterval(() => {
      setCurrentTime(new Date().toLocaleTimeString());
    }, 1000);

    return () => {
      mounted = false;
      supabaseService.supabase.removeChannel(channel);
      clearInterval(pollInterval);
      clearInterval(clockIntervalId);
    };
  }, [loadData]);

  const renderContent = () => {
    if (error) {
      return (
        <ErrorWrapper>
          <ErrorTitle>Bir Sorun Olutu</ErrorTitle>
          <ErrorMessage>{error}</ErrorMessage>
        </ErrorWrapper>
      );
    }

    if (!hasActiveRecords && !loading) {
      return (
        <BigLogoContainer>
          <BigLogo src="/logo.svg" alt="06 Global Lojistik" />
          <LogoText>06 Global Lojistik</LogoText>
          <LogoSubText>Tamaclk Hizm. San. ve Tic. Ltd. ti.</LogoSubText>
        </BigLogoContainer>
      );
    }

    return <TransportTable records={displayRecords} />;
  };

  if (loading && records.length === 0) {
    return <LoadingDisplay />;
  }

  if (stableLayout === false) {
    return (
      <DashboardContainer $isEmpty={true}>
        <Header $customColor={headerColor}>
          <SmallParticles><span /><span /></SmallParticles>
          <Title>{pageTitle}</Title>
          <ClockGroup>
            <UpdateInfo>{!isOnline && "(evrimd)"}</UpdateInfo>
            <TimeDisplay>{currentTime}</TimeDisplay>
          </ClockGroup>
        </Header>
        <ContentArea $isEmpty={true}>
          <EmptyBackground>
            <BigLogoContainer>
              <BigLogo src="/logo.svg" alt="06 Global Lojistik" />
              <LogoText>06 Global Lojistik</LogoText>
              <LogoSubText>Tamaclk Hizm. San. ve Tic. Ltd. ti.</LogoSubText>
            </BigLogoContainer>
          </EmptyBackground>
        </ContentArea>
      </DashboardContainer>
    );
  }

  return (
    <DashboardContainer $isEmpty={false}>
      <Header $customColor={headerColor}>
        <SmallParticles><span /><span /></SmallParticles>
        <Title>{pageTitle}</Title>
        <ClockGroup>
          <UpdateInfo>{!isOnline && "(evrimd)"}</UpdateInfo>
          <TimeDisplay>{currentTime}</TimeDisplay>
        </ClockGroup>
      </Header>

      <ContentArea $isEmpty={!hasActiveRecords}>
        {renderContent()}
      </ContentArea>

      <Footer $customColor={headerColor}>
        <SmallParticles><span /><span /></SmallParticles>
        <ContactInfo>
          <PhoneIcon>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M6.62 10.79c1.44 2.83 3.76 5.15 6.59 6.59l2.2-2.2c.27-.27.67-.36 1.02-.24 1.12.37 2.33.57 3.57.57.55 0 1 .45 1 1V20c0 .55-.45 1-1 1-9.39 0-17-7.61-17-17 0-.55.45-1 1-1h3.5c.55 0 1 .45 1 1 0 1.25.2 2.45.57 3.57.11.35.03.74-.25 1.02l-2.2 2.2z" />
            </svg>
          </PhoneIcon>
          {phoneNumber}
        </ContactInfo>
      </Footer>
    </DashboardContainer>
  );
};

export default Dashboard;
